.new-quote-form-container {
  display: grid;
  grid-template-columns: 61px auto;
  height: 100%;

  .new-quote-form-img-container {
    background-color: #fe5e41;
    height: 100%;
    cursor: default;
    display: flex;
    align-items: flex-end;
  }

  .new-quote-form {
    display: flex;
    flex-direction: column;
    place-content: center;

    .new-quote-message {
      margin: 0 0 100px 0;
      text-align: center;
      font-size: 28px;
      font-weight: 500;
    }

    .first-last-name-group {
      display: flex;
      width: 100%;
      justify-content: space-around;
      margin: 0 0 50px 0;
      text-align: center;

      .first-name,
      .last-name {
        display: flex;

        flex-direction: column;

        input {
          padding: 3px;
          font-size: 15px;
          border-top: none;
          border-left: none;
          border-right: none;
          border-bottom: 1px solid #000;
          font-family: "Montserrat", "sans-serif";
          width: 220px;
          text-align: center;

          &:focus {
            outline: none;
            border-bottom: 1px solid #fe5e41;
          }
        }

        label {
          font-weight: 300;
        }
      }
    }

    .quote-input-group {
      display: flex;
      flex-direction: column;
      text-align: center;
      margin: 0 55px 60px 55px;

      textarea {
        padding: 20px 0 0 0;
        font-size: 16px;
        border-top: 1px solid #000;
        border-left: none;
        border-right: none;
        border-bottom: 1px solid #000;
        font-family: "Montserrat", "sans-serif";
        text-align: center;
        max-height: 200px;

        &:focus {
          outline: none;
          border-top: 1px solid #fe5e41;
          border-bottom: 1px solid #fe5e41;
        }
      }

      label {
        font-weight: 300;
      }
    }

    .new-quote-action-buttons {
      display: flex;
      justify-content: space-around;
      margin: 0 0 55px 0;
      button {
        width: 165px;
        border: 1.3px solid #000;
        background: none;
        padding: 5px;
        font-size: 17px;
        font-family: "Montserrat", "sans-serif";

        &:hover {
          cursor: pointer;
          background: #fe5e41;
          transition: 0.2s ease-in-out;
          color: #ffff;
        }
      }
    }

    span {
      display: block;
    }
  }

  .sent-banner,
  .invalid-banner {
    font-size: 20px;
    font-weight: 500;
    text-align: center;
  }

  .sent-banner {
    color: #7cb03d;
  }

  .invalid-banner {
    color: #ed0e0e;
  }
}
